import React, { PureComponent } from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  message
} from 'antd'
import style from './style/Form.scss'
import { CsxSelect, CsxMultipleSelect } from 'Form'
import { connect } from 'react-redux'
import { API_USER_WAREHOUSELIST, API_PRODUCTS_KEYVALUES } from 'api'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.goodsOverAllocation, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }

  //查询
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    handleGetList({ searchParams: { ...searchParams, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update, handleGetList, size } = this.props
    update({ searchParams: {} })
    handleGetList({ searchParams: { page: 1, size } })
  }

  render() {
    const {
      searchParams: {
        bigCategoryCode,
        warehouseCode,
      },
      modalTuttonLoading,
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              label="大类"
              dateApi={API_PRODUCTS_KEYVALUES}
              searchParams={{ type: 2 }}
              selectItem={{ data: bigCategoryCode, key: 'categoryCode', valueKey: ['categoryCode', 'categoryName'] }}
              onChangeSelect={(value) => {
                this.onInputChange({ bigCategoryCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSelect
              label='仓库'
              dateApi={API_USER_WAREHOUSELIST}
              selectItem={{ data: warehouseCode ? warehouseCode :{value: userLoation.value, children: `${userLoation.value}`}, key: 'warehouseCode', valueKey: 'warehouseCode' }}
              onChangeSelect={(value) => {
                this.onInputChange({ warehouseCode: value })
              }}
            />
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" style={{ marginRight: 20 }} loading={modalTuttonLoading} onClick={this.query}>查询</Button>
              <Button type="primary" onClick={this.reset}>重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



