import * as actionType from './actionType'
import {getData} from 'util'
import {
  API_OVERCHARGE_LIST_PAGE, 
  API_OVERCHARGE_CREATE_SETING, 
  API_OVERCHARGE_ID_DELETE, 
  API_WMS_OVERCHARGE_UPDATE
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.GOODSOVERALLOCATION_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  console.log(reqData,9999)
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if(Object.prototype.toString.call(reqObj[i]) == '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  let {size, page} = reqData.searchParams
  dispatch({type: actionType.GOODSOVERALLOCATION_UPDATE, data: {tableLoading: true}})
  getData({api: API_OVERCHARGE_LIST_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.GOODSOVERALLOCATION_UPDATE,
      data: {
        size,
        page,
        total: data.result.total,
        tableLoading: false,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.GOODSOVERALLOCATION_FILE
    })
  })
}

// 创建超收配置
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.GOODSOVERALLOCATION_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_OVERCHARGE_CREATE_SETING
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.GOODSOVERALLOCATION_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.GOODSOVERALLOCATION_FILE
    })
  })
}

// 编辑修改超收配置
export const handleEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.GOODSOVERALLOCATION_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_WMS_OVERCHARGE_UPDATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.GOODSOVERALLOCATION_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.GOODSOVERALLOCATION_FILE
    })
  })
}

// 删除
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_OVERCHARGE_ID_DELETE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.GOODSOVERALLOCATION_FILE
    })
  })
}





