import React, { PureComponent } from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  message
} from 'antd'
const FormItem = Form.Item
import { API_USER_WAREHOUSELIST, API_PRODUCTS_KEYVALUES } from 'api'
import { CsxFormSelect, CsxFormMultipleSelect } from 'Form'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.goodsOverAllocation, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const { modalType, update, userLoation, modalData } = this.props
    //创建
    if (modalType == 0) {
      update({ modalData: { warehouseCode: { value: userLoation.value, children: userLoation.children } } })
    }
    //编辑
    if (modalType == 1) {
      console.log(modalData, 333)
      let reqObj = { ...modalData }
      reqObj.warehouseCode = {
        value: reqObj.warehouseCode,
        children: reqObj.warehouseName
      }
      reqObj.bigCategoryCode = {
        value: reqObj.bigCategoryCode,
        children: reqObj.bigCategoryName
      }
      update({ modalData: { ...reqObj } })
    }
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }

  // 改变当前的输入值
  onInputChange(values) {
    const { modalData, update } = this.props
    update({ modalData: { ...modalData, ...values } })
  }

  // 提交
  handleSubmit = (e) => {
    e.preventDefault()
    const { form, handleAddOrEdit, handleGetList, handleEdit, modalData, modalType, userLoation, size, searchParams } = this.props
    form.validateFieldsAndScroll((err, fieldsValue) => {
      if (!err) {
        // return;
        let reqObj = { ...modalData }
        //创建
        reqObj.bigCategoryName = reqObj.bigCategoryCode && reqObj.bigCategoryCode.children
        reqObj.bigCategoryCode = reqObj.bigCategoryCode && reqObj.bigCategoryCode.value
        reqObj.warehouseName = userLoation.children
        reqObj.warehouseCode = userLoation.value
        if (reqObj)
          console.log(reqObj)
        if (modalType == 0) {
          handleAddOrEdit({ data: reqObj }, () => {
            handleGetList({ searchParams: { page: 1, size } })
            message.success('创建成功')
          })
        } else {
          //编辑
          reqObj.id = modalData.id
          handleEdit({ data: { ...reqObj } }, () => {
            handleGetList({ searchParams: { warehouseCode: searchParams.warehouseCode, page: 1, size } })
            message.success('编辑成功')
          })
        }
      }
    })
  }

  render() {
    const {
      modalType,
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData,
    } = this.props
    // console.log(modalType)
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      }
    }
    return (
      <Modal
        title={modalType == 0 ? '创建配置' : '编辑配置'}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='640px'
      >
        <Form onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              <FormItem {...formItemLayout} label={"仓库"}>
                {getFieldDecorator('warehouseCode', {
                  initialValue: modalData.warehouseCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_USER_WAREHOUSELIST}
                    antd={{ disabled: true }}
                    selectItem={{ key: 'key', valueKey: ['key', 'value'] }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={24}>
              <FormItem {...formItemLayout} label={"大类"}>
                {getFieldDecorator('bigCategoryCode', {
                  initialValue: modalData.bigCategoryCode,
                  rules: [{
                    required: true, message: '请选择!'
                  }]
                })(
                  <CsxFormSelect
                    antd={{ disabled: modalType == 1 ? true : false }}
                    dateApi={API_PRODUCTS_KEYVALUES}
                    searchParams={{ type: 2 }}
                    selectItem={{ key: 'categoryCode', valueKey: ['categoryCode', 'categoryName'] }}
                    handleSelectChange={(data) => {
                      this.onInputChange({ bigCategoryCode: data })
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={24}>
              <FormItem {...formItemLayout} label={"超收比例"}>
                {getFieldDecorator('overchargeProportion', {
                  initialValue: modalData.overchargeProportion,
                  rules: [{
                    required: true, message: '请填写!',
                  }, {
                    pattern: /^[0-9]+(\.[0-9]{1,2})?$/, message: '超收比例最多保留两位小数'
                  }]
                })(
                  <Input onChange={(e) => this.onInputChange({ overchargeProportion: e.target.value })} />
                )}
              </FormItem>
            </Col>


          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{ marginRight: '48px' }} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



